.social {
    display: flex;
    text-align: center;
    flex-direction: row;
    justify-content: center;
}

.social__logo {
    width: 69px;
    height: 69px;
}

.social .social__link {
    display: flex;
    align-items: center;
}

.social .social__label {
    margin-left: 7px;
    font-weight: 400;
    font-size: 21px;
    line-height: 24px;
}

.social .social__link-wrap + .social__link-wrap {
    margin-left: 1.78vw;
}

@media (max-height: 859px) or (max-width: 1489px) {
    .social__logo {
        width: 49px;
        height: 49px;
    }

    .social .social__label {
        margin-left: 5px;
        font-size: 15px;
        line-height: 17px;
    }
}

@media (orientation: landscape) and (max-width: 1024px) {
    .social__logo {
        width: 40px;
        height: 40px;
    }

    .social .social__label {
        margin-left: 4px;
        font-size: 12px;
        line-height: 14px;
    }
}

@media (orientation: portrait) and (max-width: 1024px) {

}

@media (max-width: 630px) {
    .social .social__label {
        display: none;
    }
}