@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #ffffff;
  color: black;
  font-family: 'Bebas', sans-serif;
  font-weight: 300;
}

.wrapper {
  width: 1280px;
  margin: 50px auto;
}

header .navbar {
  display: inline-block;
  width: 100%;
}

header .logo {
  margin: 20px 0;
  background: url("img/logo.png");
  width: 10%;
  height: 50px;
  background-size: cover;
  background-position: center;
}

header .presentation {
  margin: 100px 0;
  background: url("img/assa.png") no-repeat;
  width: 100%;
  height: 341px;
  background-size: cover;
  background-position: center;
}

header ul.nav {
  float: right;
  list-style: none;
  margin-top: -53px;
}

header ul.nav li {
  display: inline;
  margin-left: 25px;
  cursor: pointer;

}

footer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
}

footer .rights {
  margin: auto;
}

.content {
  margin-top: 100px;
  display: flex;
}

.photo {
  margin-left: 50px;
  background: url("img/assa.png") no-repeat;
  width: 90%;
  height: 341px;
  background-size: cover;
  background-position: center;
}

.about {
  margin-top: 20px;
  width: 400px;
}


.serviceBar {
  margin-top: 100px;
  display:  flex;
  justify-content: center;
  align-items: center;
}

.serviceHeading {
  margin-top: 50px;
  display: flex;
  align-items: center;
}

main {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
}

main .item {
  margin: 30px 30px 30px 30px ;
  overflow: hidden; /* чтобы при трансформации не выходило за рамки */
}

main .item img {
  margin-bottom: 30px;
  transition: transform 500ms ease; /* замедление */
}

main .item img:hover {
  transform: scale(1.10); /* увеличение при наводке */
}


